// @hotwired/stimulus 
import { Application } from "../../node_modules/@hotwired/stimulus/dist/stimulus.umd.js"
import HelloController from "./controllers/hello_controller";

const application = Application.start();

// HelloController 
application.register("hello", HelloController);

// 
document.addEventListener('turbo:load', () => {
  // 
  const addTaskForm = document.getElementById('add-task-form');
  if (addTaskForm) {
    addTaskForm.addEventListener('submit', function(event) {
      event.preventDefault();
      const taskName = document.getElementById('task-name').value;
      console.log(`: ${taskName}`);
      addTaskForm.reset();
    });
  }

  // 
  document.querySelectorAll('.task-complete-checkbox').forEach(checkbox => {
    checkbox.addEventListener('change', function() {
      const taskId = this.dataset.taskId;
      const taskRow = document.querySelector(`tr[data-task-id="${taskId}"]`); // 

      console.log(':', taskId);
      console.log(` ${taskId}  ${this.checked ? '' : ''} `);

      fetch(`/tasks/${taskId}/toggle_completion`, {
        method: 'PUT',
        headers: {
          'X-CSRF-Token': document.querySelector("[name='csrf-token']").getAttribute('content'),
          'Content-Type': 'application/json',
        },
        body: JSON.stringify({ completed: this.checked })
      })
      .then(response => {
        if (response.ok) {
          console.log('');
          if (this.checked) {
            taskRow.classList.add('completed');
            console.log(` ${taskId}  'completed' `);
          } else {
            taskRow.classList.remove('completed');
            console.log(` ${taskId}  'completed' `);
          }
        }
        return response.json();
      })
      .then(data => {
        console.log(':', data);
      })
      .catch(error => {
        console.error(':', error);
      });
    });
  });

  // 
  document.querySelectorAll('.delete-task-button').forEach(button => {
    button.addEventListener('click', function() {
      const taskId = this.dataset.taskId;
      console.log(` ${taskId} `);
      // 
    });
  });
});