// Stimulus
import { Controller } from "../../../node_modules/@hotwired/stimulus/dist/stimulus.umd.js"

export default class extends Controller {
  static targets = ["complete"];

  // 
  connect() {
    this.element.textContent = "TaskMaster2!";
    console.log("HelloController");
  }

  // 
  toggleComplete(event) {
    event.preventDefault();
    const taskId = this.element.dataset.id; // data-idID
    console.log(` ${taskId} `);

    // API
    fetch(`/tasks/${taskId}/toggle_completion`, {
      method: 'PUT',
      headers: {
        'X-CSRF-Token': document.querySelector("[name='csrf-token']").getAttribute('content'),
        'Content-Type': 'application/json',
      },
      body: JSON.stringify({ completed: this.completeTarget.classList.contains("is-complete") })
    })
    .then(response => {
      if (response.ok) {
        console.log(` ${taskId} `);
        this.completeTarget.classList.toggle("is-complete"); // CSS
      } else {
        console.error(` ${taskId} `);
      }
    })
    .catch(error => {
      console.error('API:', error);
    });
  }
}
